const fileTypeIcon = (typeName) => {
  let iconSrc = "";
  switch (typeName) {
    case "":
      iconSrc = require("@/assets/images/icon/ic-audio.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-video.svg");
      break;
    case "OX":
      iconSrc = require("@/assets/images/icon/ic-ox.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-worksheet.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-brains.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-walking.svg");
      break;
    case "ADL":
      iconSrc = require("@/assets/images/icon/ic-adl.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-subjective.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-multiplechoice.svg");
      break;
    case "":
      iconSrc = require("@/assets/images/icon/ic-stretching.svg");
      break;
    default:
      break;
  }
  return iconSrc;
};

export default fileTypeIcon;
